 /* Smartphones (landscape) ----------- */
@media only screen 
and (min-width : 321px) {
	.container {
		width: 90%;
	}
}
 
/* Smartphones (portrait) ----------- */
@media only screen 
and (max-width : 320px) {
	.container {
		width: 95%;
	}
}
 
/* iPads (landscape) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : landscape) {
	.container {
		width: 75%;
	}
}
 
/* iPads (portrait) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : portrait) {
	.container {
		width: 80%;
	}
}
/* Laptops ----------- */
@media only screen 
and (min-device-width : 1024px) 
and (max-device-width : 1440px) {
	.container {
		width: 60%;
	}
}

/* Laptops ----------- */
@media only screen 
and (min-device-width : 1440px) {
	.container {
		width: 50%;
	}
}

.container {
	margin: 0 auto;
	margin-top: 7%;
	border: 1px solid #1C7430;
}

.top-bar {
	
	margin-bottom: 45px;
}



