.start-game-container-center {
	text-align: center;
	margin: 0 auto;
}

.img-container {
	height: auto;
	width: 100%;
	margin: 0 auto;
	margin-top: 7%;
}

.img-container img {
	width: 50%;
}

h1.title {
	font-family: "Open Sans";
	font-weight: bold;
	opacity: 0.85;
	font-size: 28px;
	color: #FFFFFF;
	letter-spacing: 0.2px;
	margin-top: 2%;
}
.btn {
	font-family: "Open Sans";
	font-weight: bold;
	display: inline-block;
	color: #1C7430;
	background-color: #FFFFFF;
	opacity: 0.85;
	padding: 10px 20px;
	width: 144px;
	border-radius: 4px;
}
.start-game-btn {
	margin-top: 4%;
}

.rerty-game-btn {
	margin-top: 1%;
	margin-left: 1%;
	
}


