.Card {
	width: 100%; 
	position: relative;
	display: block;
}
.Card img{
	width: 100%;
	height: auto;
/*	display: block;*/
}

 /* Smartphones (landscape) ----------- */
@media only screen 
and (min-width : 321px) {
	.img-container {
		width: 60%;
	}
}
 
/* Smartphones (portrait) ----------- */
@media only screen 
and (max-width : 320px) {
	.img-container {
		width: 72%;
	}
}
 
/* iPads (landscape) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : landscape) {
	.img-container {
		width: 78%;
	}
}
 
/* iPads (portrait) ----------- */
@media only screen 
and (min-device-width : 768px) 
and (max-device-width : 1024px) 
and (orientation : portrait) {
	.img-container {
		width: 82%;
	}
}
/* Laptops ----------- */
@media only screen 
and (min-device-width : 1024px) 
and (max-device-width : 1440px) {
	.img-container {
		width: 92%;
	}
}

/* Laptops ----------- */
@media only screen 
and (min-device-width : 1440px) {
	.img-container {
		width: 100%;
	}
}


